import React, { Component } from 'react'
import {con} from './Context'
export default class Child extends Component {
    render() {
        return (
            <div>
                Child组件
                <con.Consumer>
                    {
                        (id)=><div>{id}</div>
                    }
                </con.Consumer>
                
            </div>
        )
    }
}
Child.contextType = con;